<template>
  <div>
      <v-alert
      class="text-left"
      border="right"
      dense
      colored-border
      type="error"
      elevation="2"
      v-if="!grafanaIp"
    >You need to set up GlobalIp GrafanaIp</v-alert>
    <v-alert
      class="text-left"
      border="right"
      dense
      colored-border
      type="error"
      elevation="2"
      v-if="!grafanaPort"
      >You need to set up GrafanaPort</v-alert>
    <iframe frameBorder="0" :src="grafanaUrl" />
  </div>
</template>
<script>
export default {
  data:()=>({
    grafanaIp: "",
    grafanaPort: "",
    grafanaUrl:"",
  }),
  mounted(){
      const { setting }=this.$store.state;
      this.grafanaIp = setting.isGlobal ? setting.globalIp : setting.grafanaIp;
      this.grafanaPort=setting.grafanaPort;
      this.grafanaUrl=`http://${this.grafanaIp}:${this.grafanaPort}`;
  }
};
</script>
<style scoped>
iframe{
  height: calc(100vh - 12px);
  width: calc(100% - 12px);
  float: left;
}
</style>



