<template>
  <v-app>
    <Sidebar :drawer="drawer"/>
    <v-main>
      <v-container fluid>
        <router-view></router-view>
      </v-container>
    </v-main>
    <v-dialog
      v-model="loading"
      hide-overlay
      persistent
      width="300"
    >
      <v-card color="primary" dark>
        <v-card-text>
          Loading
          <v-progress-linear
            indeterminate
            color="white"
            class="mb-0"
          ></v-progress-linear>
        </v-card-text>
      </v-card>
    </v-dialog>
  </v-app>
</template>

<script>
  import Sidebar from "./Sidebar";
  import logo from '../assets/nube-frog.png';
  import axios from "../axios";

  export default {
    components: {Sidebar},
    data() {
      return {
        user: "",
        logo: logo,
        drawer: true,
      }
    },
    computed: {
      loading () {
        return this.$store.state.loading;
      }
    },

  }
</script>
<style>
  .v-toolbar {
    flex: none;
  }
  .v-application--wrap {
    display: inline-block !important;
  }
</style>



