<template>
  <div>
    <v-data-table
      v-model="selected"
      :headers="deviceHeaders"
      :items="network.devices"
      class="elevation-1"
      item-key="uuid"
      show-select
      disable-sort
      :item-class="backgroundColor"
    >
      <template v-slot:top>
        <v-breadcrumbs :items="dirs">
          <v-breadcrumbs-item
            v-bind:class="{ 'cursor-pointer': !item.disabled }"
            slot="item"
            slot-scope="{ item }"
            @click.native="() => clickBreadcrumb(item)"
            :key="item.text">
            {{ item.text }}
          </v-breadcrumbs-item>
        </v-breadcrumbs>
        <v-toolbar flat>
          <v-toolbar-title>DEVICES</v-toolbar-title>
          <v-divider class="mx-4" inset vertical></v-divider>
          <div class="text-left pa-2">
            <v-btn @click="getData" x-small color="primary" class="white--text" >
              Refresh<v-icon x-small right dark> mdi-refresh </v-icon>
            </v-btn>&nbsp;
            <v-btn @click="clickDeleteMulti" :disabled="selected.length === 0" x-small color="error" class="white--text" >
              Delete<v-icon x-small right dark> mdi-delete </v-icon>
            </v-btn>&nbsp;
            <v-menu offset-y>
              <template v-slot:activator="{ on, attrs }">
                <v-btn x-small color="#009688" class="white--text"  dark v-bind="attrs" v-on="on" >Download<v-icon x-small right dark>mdi-download</v-icon></v-btn>
              </template>
              <v-list>
                <v-list-item link>
                  <v-list-item-title @click="clickDownload(network.devices,'csv')">CSV</v-list-item-title>
                </v-list-item>
                <v-list-item link>
                  <v-list-item-title @click="clickDownload(network.devices, 'xlsx')">Excel</v-list-item-title>
                </v-list-item>
              </v-list>
            </v-menu>&nbsp;
            <v-btn @click="clickImport" x-small color="primary" class="white--text" >
              Import<v-icon x-small right dark> mdi-import </v-icon>
            </v-btn>&nbsp;
            <v-btn @click="clickJsonExport(selected)" :disabled="selected.length !== 1" x-small color="#009688" class="white--text" >
              Export<v-icon x-small right dark> mdi-export </v-icon>
            </v-btn>
          </div>
          <v-spacer></v-spacer>
          <v-btn color="primary" dark small @click="dialog = true">
            Create Device
          </v-btn>
        </v-toolbar>
      </template>

      <template v-slot:item.name="{ item }">
        <a @click="onClickOpen(item)"> {{ item.name }} </a>
      </template>

      <template v-slot:item.points="{ item }" >
        <div>{{ item.points ? item.points.length : 0}}</div>
      </template>

      <template v-slot:item.actions="{ item }">
        <v-icon small @click="onClickEditItem(item)" color="primary">mdi-pencil</v-icon>&nbsp;
        <v-icon small @click="clickDelete(item)" color="error">mdi-delete</v-icon>&nbsp;
        <v-icon small @click="onClickOpen(item)">mdi-open-in-app</v-icon>
      </template>

      <template v-slot:no-data> No data </template>
    </v-data-table>

    <v-dialog v-model="dialog" max-width="500px">
      <v-card>
        <v-card-title>
          <span class="headline">{{ formTitle }}</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-form v-model="valid" ref="form" lazy-validation>
              <v-row>
                <v-col cols="12" sm="12" md="12">
                  <v-text-field v-model="network.name" disabled class="centered-input"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.name" label="device name" required :rules="[rules.required]" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-select v-model="editedItem.type" label="Type" :items="['RTU', 'TCP']" required :rules="[rules.required]"></v-select>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-checkbox v-model="editedItem.enable" label="enable" required ></v-checkbox>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.tcp_ip" label="TCP ID Address" required :rules="[rules.required]" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.tcp_port" label="TCP Port" required :rules="[rules.required]" type="number" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-checkbox  v-model="editedItem.zero_mode" label="zero mode" ></v-checkbox>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.ping_point_type" label="ping poin type" required :rules="[rules.required]" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.ping_point_address" label="ping point address" required :rules="[rules.required]" type="number" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-checkbox v-model="editedItem.timeout_global" label="timeout global" ></v-checkbox>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.timeout" label="timeout" required :rules="[rules.required]" type="number" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.address" label="address" required :rules="[rules.required]" type="number" ></v-text-field>
                </v-col>
              </v-row>
            </v-form>
          </v-container>
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="close">Cancel</v-btn>
          <v-btn color="blue darken-1" text @click="save" :disabled="!valid" >Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-dialog v-model="dialogImport" max-width="500px">
      <v-card>
        <v-card-title>
          <span class="headline">Import Device</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-textarea
              outlined
              name="input-7-4"
              label="import data"
                v-model="dataImport"
            ></v-textarea>
          </v-container>
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="closeDialogImport">Cancel</v-btn>
          <v-btn color="blue darken-1" text @click="saveImport" :disabled="!dataImport">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-dialog v-model="dialogDelete" persistent max-width="290">
      <v-card>
        <v-card-title> Confirm Delete Device</v-card-title>
        <v-card-text>
          <v-container>
            <v-row>Are you sure?</v-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="closeDelete"> No </v-btn>
          <v-btn color="red darken-1" text @click="confirm"> Yes </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { substringType, copyToClipboard, isJsonString, exportJSON, exportCSV, exportExcel } from "../utils/utils";
import Points from "./Points";
import axios from "../axios";

export default {
  components: { "points-table": Points },
  data: () => ({
    valid: false,
    dialog: false,
    dialogDelete: false,
    dialogImport: false,

    deviceHeaders: [
      { text: "Actions", value: "actions" },
      { text: "Name", value: "name", align: "start" },
      { text: "Fault", value: "fault" },
      { text: "Type", value: "type" },
      { text: "Enable", value: "enable" },
      { text: "Address", value: "address" },
      { text: "IP", value: "tcp_ip" },
      { text: "Port", value: "tcp_port" },
      { text: "Points", value: "points" },
      { text: "UUID", value: "uuid", align: "start" },
    ],
    editedIndex: -1,
    editedItem: {
      name: "",
      type: "RTU",
      enable: false,
      address: 0,
      tcp_ip: "",
      tcp_port: 0,
      ping_point_type: "",
      ping_point_address: 0,
      zero_mode: false,
      timeout: 0,
      timeout_global: false,
      network_uuid: "",
    },
    defaultItem: {
      name: "",
      type: "RTU",
      enable: false,
      address: 0,
      tcp_ip: "",
      tcp_port: 0,
      ping_point_type: "",
      ping_point_address: 0,
      zero_mode: false,
      timeout: 0,
      timeout_global: false,
      network_uuid: "",
    },
    currentItem: {},
    dataImport: "",
    rules: { required: (value) => !!value || "Required." },
    network: {},
    selected: [],
    action: "",
    objectKeys: Object.keys,
    objectValues: Object.values,
    dirs: [{text: '..', link: 'modbus'}],

  }),
  computed: {
    formTitle() {
      return this.editedIndex === -1 ? "Create Device" : "Edit Device";
    },
  },
  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },
  mounted() {
    this.getData();
  },
  methods: {
    getData() {
      axios.get(`bac-rest/network/${this.$route.query.network_uuid}`)
        .then((data) => {
          this.network = data;
          //get dir
          if(this.dirs.length != 2){
            this.dirs = [{text: '..', link: 'modbus'}],
            this.dirs.push({text: this.network.name, disabled: true})
          }
          this.editedItem.network_uuid = this.defaultItem.network_uuid = this.network.uuid;
          this.network.devices = data.devices.map((item) => {
            item.type = substringType(item.type);
            return item;
          });
        });
    },

    onClickOpen(item) {
      this.$router.push({ name: "points", query: { device_uuid: item.uuid } });
    },
    clickBreadcrumb(dir) {
      if(!dir.disabled){
        this.$router.push({ name: dir.link});
      }
    },

    onClickEditItem(item) {
      this.editedIndex = this.network.devices.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    async save() {
      if (this.editedIndex > -1) {
        this.updateDevice(this.editedItem)
      } else {
        this.createDevice(this.editedItem)
      }
      this.close();
    },

    async clickDelete(item) {
      this.action = "deleteSingle";
      this.dialogDelete = true;
      this.currentItem = item;
      this.editedItem = Object.assign({}, item);
    },
    async clickDeleteMulti() {
      this.dialogDelete = true;
      this.action = "deleteSelected";
    },
    async deleteSingle() {
      this.editedIndex = this.network.devices.indexOf(this.currentItem);
      this.network.devices.splice(this.editedIndex, 1);
      await axios.delete(`bac-rest/device/${this.currentItem.uuid}`);
      this.closeDelete();
    },
    async deleteSelected() {
      const promises = [];
      for (const item of this.selected) {
        this.currentItem = item;
        promises.push(this.deleteSingle());
      }
      await Promise.all(promises);
      this.selected = [];
    },

    async confirm() {
      await this[this.action]();
      this.dialog = false;
      this.action = "";
    },

    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    closeDialogImport() {
      this.dialogImport = false;
      this.$nextTick(() => {
        this.dataImport = "";
      });
    },

    saveImport() {
      if(!isJsonString(this.dataImport)){
        return this.$toasted.show("incorrect data type!", {type: 'error'});
      }
      this.createDevice(JSON.parse(this.dataImport));
      this.closeDialogImport();
      this.selected = [];
    },

    clickImport(){
      this.dialogImport = true;
      this.dataImport = JSON.stringify(this.selected[0]);
    },
    clickJsonExport(){
      const status = copyToClipboard(JSON.stringify(this.selected[0]));
      const msg = status ? `Clipboard Copy Successful!` : `Failure on copying on clipboard!`;
      const type = status ? `success` : `error`;
      return this.$toasted.show(msg, {type: type});
    },
    clickDownload(array, type){
      const data = this.convertExportData(array);
      type === 'csv' ? exportCSV("devices", data) : exportExcel("devices", data);
    },
    convertExportData(array){
      const data = _.cloneDeep(array).map(device=>{
        _.forIn(device, (value, key) => {
          if (key == 'points') { //get points
            _.forEach(device.points, (point, indexP)=>{
                _.forIn(point, (point_value, point_key) => {
                  if(point_key != 'point_store'){
                      const pointKey = `point[${indexP}].${point_key}`;
                      device = {...device, [pointKey] : point_value}
                  }else{
                    //point_store
                    _.forIn(point.point_store, (point_store_value, point_store_key) => {
                      const pointStoreKey = `point[${indexP}].point_store.${point_store_key}`;
                      device = {...device, [pointStoreKey] : point_store_value}
                    });
                    delete point.point_store
                  }
                });
              });
            delete device.points;
          }
        });
        return device;
     });
      return data;
    },

    updateDevice(data) {
      axios.put(`bac-rest/device/${this.editedItem.uuid}`, data )
        .then((response) => {
          const index = this.network.devices.findIndex(d => d.uuid == response.uuid);
          response.type = substringType(response.type);
          Object.assign(this.network.devices[index], response);
          return this.$toasted.show("update success");
        })
        .catch((error) => { this.$toasted.show("update fail", {type: 'error'});});
    },

    createDevice(data) {
      axios.post( `bac-rest/device`, data )
        .then((response) => {
          response.type = substringType(response.type);
          this.network.devices.push(response);
          this.$toasted.show("create success");
          if(data.points && data.points.length > 0){
            for (const point of data.points) {
              point.device_uuid = response.uuid;
              this.createPoint(point);
            }
          }
        })
        .catch((error) => { this.$toasted.show("create fail", {type: 'error'}); });
    },

    async createPoint(data) {
      data.type = substringType(data.type);
      data.data_type = substringType(data.data_type);
      data.data_endian = substringType(data.data_endian);
      data.history_type = substringType(data.history_type);
      axios.post( `bac-rest/point`, data )
        .then((response) => {
          const index = this.network.devices.findIndex(p => p.uuid == response.device_uuid);
          this.network.devices[index].points.push(response);
        })
        .catch((error) => { });
    },

    backgroundColor(item) {
      let classColor = '';
      if(item.fault){
        classColor = 'background-fault-true';
      }else{
        classColor = !item.enable ? 'background-enable-false' : '';
      }
      return classColor;
    },

  },
};
</script>
<style scoped>
  .centered-input >>> input {
      text-align: center
  }
  .cursor-pointer{
    cursor: pointer;
    color: #1867c0;
  }
  .background-fault-true{
    background-color: #fbb93e;
  }
  .background-enable-false{
    background-color: #666666;
  }
</style>



