<template>
<v-card-text>
    <v-row>
      <v-col class="text-left" xl="3" lg="3" sm="12">
        <v-btn x-small color="primary" @click="clickImport" >
          Import
          <v-icon small>mdi-import</v-icon>
        </v-btn>
        <v-divider inset vertical></v-divider>
        <v-btn x-small color="pink" @click="clickJsonExport">
          Export
          <v-icon small>mdi-export</v-icon>
        </v-btn>
      </v-col>
      <v-col xl="6" lg="6" sm="12">
        <h1>Settings</h1>
      </v-col>
    </v-row>
    <br />
    <div style="text-align: start">
      <v-expansion-panels>

        <v-expansion-panel class="mb-1">
          <v-expansion-panel-header>SHOW / HIDE SIDEBAR</v-expansion-panel-header>
          <v-expansion-panel-content>
            <v-row>
              <v-col sm="3">
                <v-checkbox label="Services Manager" v-model="setting.services" @change="save" />
              </v-col>
              <v-col sm="3">
                <v-checkbox label="File Manager" v-model="setting.fileManager" @change="save" />
              </v-col>
              <v-col sm="3">
                <v-checkbox label="Point Server" v-model="setting.updateBacRest" @change="save" />
              </v-col>
              <v-col sm="3">
                <v-checkbox label="Update BACnet Server" v-model="setting.updateBacnet" @change="save" />
              </v-col>
              <v-col sm="3">
                <v-checkbox label="Update Wires" v-model="setting.updateWires" @change="save" />
              </v-col>
              <v-col sm="3">
                <v-checkbox label="Wires Flow Editor" v-model="setting.wires" @change="save" />
              </v-col>
              <v-col sm="3">
                <v-checkbox label="Mobus" v-model="setting.modbus" @change="save" />
              </v-col>
              <!-- <v-col sm="3"> -->
                <!-- <v-checkbox label="Bacnet Server" v-model="setting.bacnetServer" @change="save" /> -->
              <!-- </v-col> -->
              <v-col sm="3">
                <v-checkbox label="Grafana" v-model="setting.grafana" @change="save" />
              </v-col>
            </v-row>
          </v-expansion-panel-content>
        </v-expansion-panel>

        <v-expansion-panel class="mb-1">
          <v-expansion-panel-header>SERVICE SETTING</v-expansion-panel-header>
          <v-expansion-panel-content>
            <v-row>
              <v-col sm=4>
                <v-card outlined class="card-height">
                  <v-card-title>
                    General
                    <v-row align="center" justify="end" >
                      <v-avatar size="35">
                        <img alt="logo" :src="logo">
                      </v-avatar>
                    </v-row>
                  </v-card-title>
                  <v-card-text>
                    <v-text-field label="Device user" v-model="setting.deviceUser" ></v-text-field>
                    <v-text-field label="Build Dir" v-model="setting.buildDir" ></v-text-field>
                    <v-row>
                      <v-col sm="5">
                        <v-switch v-model="setting.isGlobal" :label="`Global IP`" @change="change"></v-switch>
                      </v-col>
                      <v-col sm="7">
                        <v-text-field label="Global Ip" v-model="setting.globalIp" :disabled="!setting.isGlobal"   :rules="globalIpRule"></v-text-field>
                      </v-col>
                    </v-row>
                  </v-card-text>
                </v-card>
              </v-col>
              <v-col sm=4>
                <v-card outlined class="card-height">
                  <v-card-title>
                    Points Server
                    <v-row align="center" justify="end" >
                      <v-avatar size="35">
                        <img alt="logo" :src="logo">
                      </v-avatar>
                    </v-row>
                  </v-card-title>
                  <v-card-text>
                    <v-text-field label=" Path" v-model="setting.bacRestPath" ></v-text-field>
                    <v-text-field label="Lib Path" v-model="setting.bacRestLibPath" ></v-text-field>
                    <v-row>
                      <v-col sm="6">
                        <v-text-field :disabled="setting.isGlobal" label="Ip" v-model="setting.bacRestIp"></v-text-field>
                      </v-col>
                      <v-col sm="6">
                        <v-text-field label="Port" v-model="setting.bacRestPort" ></v-text-field>

                      </v-col>
                    </v-row>
                  </v-card-text>
                </v-card>
              </v-col>
              <v-col sm=4>
                <v-card outlined class="card-height">
                  <v-card-title>
                    BACnet Server
                    <v-row align="center" justify="end" >
                      <v-avatar size="35">
                        <img alt="logo" :src="logo">
                      </v-avatar>
                    </v-row>
                  </v-card-title>
                  <v-card-text>
                    <v-text-field label="Path" v-model="setting.bacnetPath"></v-text-field>
                    <v-text-field label="Lib Path" v-model="setting.bacnetLibPath"></v-text-field>
                    <v-row>
                      <v-col sm="6">
                        <v-text-field :disabled="setting.isGlobal" label="Ip" v-model="setting.bacnetServerIp" ></v-text-field>
                      </v-col>
                      <v-col sm="6">
                        <v-text-field label="Port" v-model="setting.bacnetServerPort"></v-text-field>
                      </v-col>
                    </v-row>
                    <v-text-field style="visibility: hidden;"></v-text-field>
                  </v-card-text>
                </v-card>
              </v-col>
              <v-col sm=4>
                <v-card outlined class="card-height">
                  <v-card-title>
                    Rubix Services
                    <v-row align="center" justify="end" >
                      <v-avatar size="35">
                        <img alt="logo" :src="logo">
                      </v-avatar>
                    </v-row>
                  </v-card-title>
                  <v-card-text>
                    <v-text-field :disabled="setting.isGlobal" label="Ip" v-model="setting.smonIp" ></v-text-field>
                    <v-text-field label="Port" v-model="setting.smonPort" ></v-text-field>
                    <v-text-field style="visibility: hidden;"></v-text-field>
                  </v-card-text>
                </v-card>
              </v-col>
              <v-col sm=4>
                <v-card outlined class="card-height">
                  <v-card-title>
                    Wires
                    <v-row align="center" justify="end" >
                      <v-avatar size="35">
                        <v-icon>mdi-transit-connection-variant</v-icon>
                      </v-avatar>
                    </v-row>
                  </v-card-title>
                  <v-card-text>
                    <v-text-field label="Path" v-model="setting.wiresPath"></v-text-field>
                    <v-row>
                      <v-col sm="6">
                        <v-text-field :disabled="setting.isGlobal" label="IP" v-model="setting.wiresIp"></v-text-field>
                      </v-col>
                      <v-col sm="6">
                        <v-text-field label="Port" v-model="setting.wiresPort"></v-text-field>
                      </v-col>
                    </v-row>
                  </v-card-text>
                </v-card>
              </v-col>
              <v-col sm=4>
                <v-card outlined class="card-height">
                  <v-card-title>
                    Grafana
                    <v-row align="center" justify="end" >
                      <v-avatar size="35">
                       <img alt="grafana" :src="grafana">
                      </v-avatar>
                    </v-row>
                  </v-card-title>
                  <v-card-text>
                    <v-text-field :disabled="setting.isGlobal" label="Ip" v-model="setting.grafanaIp" ></v-text-field>
                    <v-text-field label="Port" v-model="setting.grafanaPort" ></v-text-field>
                  </v-card-text>
                </v-card>
              </v-col>
            </v-row>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn :loading="loading" color="blue darken-2" @click="save">Save</v-btn>
            </v-card-actions>
          </v-expansion-panel-content>
        </v-expansion-panel>

        <v-expansion-panel class="mb-1">
          <v-expansion-panel-header>SHOW / HIDE SERVICE</v-expansion-panel-header>
          <v-expansion-panel-content>
            <v-row>
              <v-col sm="3">
                <v-checkbox label="Points Service" v-model="setting.bacRestService" @change="save" />
              </v-col>
              <v-col sm="3">
                <v-checkbox label="BACnet Server Service" v-model="setting.bacnetService" @change="save" />
              </v-col>
              <v-col sm="3">
                <v-checkbox label="Rubix Wires Service" v-model="setting.wiresService" @change="save" />
              </v-col>
              <v-col sm="3">
                <v-checkbox label="Drac Service" v-model="setting.dracService" @change="save" />
              </v-col>
              <v-col sm="3">
                <v-checkbox label="BBB Service" v-model="setting.bbbService" @change="save" />
              </v-col>
              <v-col sm="3">
                <v-checkbox label="Lorawan Service" v-model="setting.lorawanService" @change="save" />
              </v-col>
              <v-col sm="3">
                <v-checkbox label="Mosquitto Service" v-model="setting.mosquittoService" @change="save" />
              </v-col>
              <v-col sm="3">
                <v-checkbox label="Platform Services Service" v-model="setting.smonService" @change="save" />
              </v-col>
            </v-row>
          </v-expansion-panel-content>
        </v-expansion-panel>

      </v-expansion-panels>
      <!--Import dialog -->
      <v-dialog v-model="dialogImport" max-width="500px">
      <v-card>
        <v-card-title>
          <span class="headline">Import</span>
        </v-card-title>

        <v-card-text>
          <v-alert
            class="text-left"
            dense
            colored-border
            type="error"
            elevation="2"
            v-show="isValidData"
          >Incorrect Data Type!</v-alert>
          <v-container>
            <v-textarea
              outlined
              name="input-7-4"
              label="import data"
              v-model="dataImport"
              @input="onChangeImportData"
            ></v-textarea>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="closeDialogImport">Cancel</v-btn>
          <v-btn color="blue darken-1" :disabled="disableSave" text v-on:click="saveImport">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    </div>
</v-card-text>
</template>

<script>
import { sidebarItems,settingFields } from '../constants/appConstant';
import axios from "../axios";
import logo from "../assets/nube-frog-green.png";
import grafana from "../assets/grafana.jpg";
import {isJsonString,copyToClipboard} from '../utils/utils';
export default {
  name: "View",
  data() {
    return {
      logo,
      grafana,
      items: [],
      globalIpRule:[],
      dialogImport:false,
      dataImport:"",
      disable:true,
      isValidData:false,
      disableSave:true,
      settingFields,
      fla:false,
      rules: {
        required: (value) => !!value || "Required.",
      },

    };
  },
  computed: {
    setting () {
      return this.$store.state.setting;
    },
    loading () {
      return this.$store.state.loading;
    }
  },
  methods: {
    async onChangeImportData(){
      let result='through';
      if(!isJsonString(this.dataImport) || this.dataImport.length<0){
        this.isValidData=true;
      }
      else {
        const data=_.cloneDeep(JSON.parse(this.dataImport));
        const arrData=Object.keys(data);
        this.isValidData=true;
        if(arrData.length !== 0){
          for(let i=0;i<arrData.length;i++){
            if(!this.settingFields.includes(arrData[i])){
              result='notFound';
              break;
            }
          }
          this.isValidData=true;
          if(result==='through'){
            this.isValidData=false;
          }
        }
      }
      if(this.dataImport[0]===`[`||this.dataImport[this.dataImport.length-1]===`]`){
        this.disable=true;
      }
      if((this.dataImport[0]===`{` && this.dataImport[this.dataImport.length-1]===`}`)){
        this.disable=false;
      }
      this.disableSave=false;
      if(
        (this.disable!==this.isValidData)||
        (this.disable && this.isValidData)
      ) {
        this.disableSave=true;
      }
    },
    async change(){
      if(!this.setting.isGlobal){
            this.globalIpRule=[];
      }
    },
    async save(){
      if(this.setting.isGlobal && !this.setting.globalIp){
        this.globalIpRule=[ v => !!v || 'Please Input Global IP'];
        return;
      }
      this.$store.commit('changeLoading', true);
      axios.put(`setting`, this.setting).then(result => {
        this.$store.commit('changeLoading', false);
        if (result === 'successful') {
          this.$store.commit('changeSetting', this.setting);
          this.$toasted.show('Success!')
        }
      }).catch(({message}) => {
        this.$store.commit('changeLoading', false);
        this.$toasted.show(message, {type: 'error'})
      });
    },
    async clickImport(){
      this.dialogImport=true;
    },
    async closeDialogImport(){
        this.dialogImport=false;
    },
    isValidImportData(arrData,settingData){
      let result='through';
      for(let i=0;i<arrData.length;i++){
        if(!settingData.includes(arrData[i])){
          result='notFound';
          break;
        }
      }
      return result;
    },
    async saveImport(){
       if(!isJsonString(this.dataImport)){
            return this.$toasted.show("incorrect data type!",{type:'error'});
        }
       else{
            const data=_.cloneDeep(JSON.parse(this.dataImport));
           const arrData=Object.keys(data);
           const isValidData=await this.isValidImportData(arrData,this.settingFields);
           if(isValidData==='notFound'){
              this.$toasted.show('data not found!',{type:'error'});
           }
           else{
              const newSettingFields=Object.assign(this.setting,data);
              this.$store.commit('changeLoading', true);
              axios.put(`setting`, this.setting).then(result => {
              this.$store.commit('changeLoading', false);
               if (result === 'successful') {
                 this.$store.commit('changeSetting',this.setting);
                 this.fla=true;
                 this.$toasted.show('Success!');
                 this.dialogImport = false;
               }
              }).catch(({message}) => {
                this.$store.commit('changeLoading', false);
                this.$toasted.show(message, {type: 'error'})
              });
            }
        }
    },
    clickJsonExport(){
     const status = copyToClipboard(JSON.stringify(this.setting));
      const msg = status ? `Clipboard Copy Successful!` : `Failure on copying on clipboard!`;
      const type = status ? `success` : `error`;
      return this.$toasted.show(msg, {type: type});
    }
  }
};

</script>
<style>
  .card-height{
    height: 295px;
  }
</style>



