<template>
  <div>
    <v-alert
      class="text-left"
      border="right"
      dense
      colored-border
      type="error"
      elevation="2"
      v-if="!ip"
    >You need to set up GlobalIp or WiresIp</v-alert>
    <v-alert
      class="text-left"
      border="right"
      dense
      colored-border
      type="error"
      elevation="2"
      v-if="!port"
    >You need to set up WiresPort</v-alert>
    <iframe frameBorder="0" :src="url" />
  </div>
</template>

<script>

export default {
  name: "wires",
  data:()=>({
    ip: "",
    port: "",
    url:"",
  }),
  mounted(){
    const { setting }=this.$store.state;
    this.ip = setting.isGlobal ? setting.globalIp : setting.wiresIp;
    this.port = setting.wiresPort;
    this.url=`http://${this.ip}:${this.port}`;
  }
};
</script>
<style scoped>
  iframe {
    height: calc(100vh - 12px);
    width: calc(100% - 12px);
    float: left;
  }
</style>



